body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.NavBar {
  z-index: 2;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: rgba(101, 103, 105, 0.897);
  
  /* padding: 10px 5px 15px 5px; */
}

.NavBar ul {
  display: flex;
  flex-flow: wrap row;
  justify-content: space-evenly;
  list-style: none;
  padding: 0;
  margin: 0;
}

.NavBar li {
  position: relative;
  right: 5px;
  margin: 10px;
}

.NavBar .page-link {
  text-decoration: none;
  color: lightgray;
}

.NavBar .page-link:hover {
  color: white;
}

p.number-of-items-in-cart {
  margin: 0;
  padding: 3px 10px;
  background: rgba(25, 190, 182, 0.897);
  border: 0px solid white;
  border-radius: 100%;
}

.number-of-items-in-cart span {
  font-weight: 800;
}

.WelcomePage {
  
}
.ProductsPage {

}

.totalCost {
  font-weight: 800;
  font-family: Arial, Helvetica, sans-serif;
}

.ProductsPage .totalCost-wrapper {
  display: flex;
  justify-content: space-evenly;
  color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 62px;
  margin: 0;
  padding: 15px 0px 15px;
  font-weight: 600;
  font-size: 125%;
  background-color: rgba(61, 61, 61, 0.9);
}

.ProductsPage .totalCost-wrapper h5 {
  margin: 0;
}

.ProductsPage .number-of-items-in-cart {
  font-size: 80%;
}

.ProductsPage h4 {
  margin: 0;
  padding: 25px 0px 25px;
  background-color: #b8b8b85c;
}

.ProductsPage ul {
  padding: 0;
  display: flex;
  flex-flow: wrap row;
  justify-content: space-evenly;
}

.ProductListItem {
  list-style: none;
  padding: 0;
  padding: 5px 15px 35px;
  margin: 25px 15px;
  border: 5px outset darkgray;
  border-radius: 15px 0px 15px 0px;
  max-width: 350px;
  background-color: rgba(153, 140, 139, 0.349);
}

.ProductListItem .image {
  border: 3px outset lightgray;
  border-radius: 15px 0px 15px 0px;
}

.ProductListItem h5 {
  margin-bottom: 5px;
}

.ProductListItem p {
  margin-top: 5px;
}

.ProductListItem button {
  cursor: pointer;
  color: white;
  font-size: 125%;
  border-color: silver;
  background-color: #13129c;
  padding: 15px;
  outline-offset: -5px;
  outline: black 3px solid;
}

.ProductListItem button:hover {
  background-color: #1111e6;
}

.ProductListItem button:active {
  border-color: blue;
}

.Cart {
  padding: 0;
}

.totalCost {
  font-weight: 800;
  font-family: Arial, Helvetica, sans-serif;
}

.Cart .totalCost-wrapper {
  display: flex;
  justify-content: space-evenly;
  color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 62px;
  margin: 0;
  padding: 15px 0px 15px;
  font-weight: 600;
  font-size: 125%;
  background-color: rgba(61, 61, 61, 0.9);
}

.Cart .totalCost-wrapper h5 {
  margin: 0;
}

.Cart h4 {
  margin: 0;
  padding: 25px 0px 25px;
  background-color: #b8b8b85c;
}

.Cart .number-of-items-in-cart {
  font-size: 80%;
}

.Cart ul {
  margin: 5px 0px 5px 0px;
  padding: 0;
  display: flex;
  flex-flow: wrap row;
  justify-content: space-evenly;
}

.CartItem {
  list-style: none;
  padding: 0;
  padding: 5px 15px 15px;
  margin: 5px;
  border: 5px outset darkgray;
  border-radius: 15px 0px 15px 0px;
  max-width: 250px;
  width: -webkit-fill-available;
  background-color: rgba(153, 140, 139, 0.349);
}

.CartItem .image {
  border: 3px outset lightgray;
  border-radius: 15px 0px 15px 0px;
}

.CartItem .title {
  margin: 5px 0px;
}

.CartItem .cost {
  margin: 5px 0px;
}

.CartItem .Remove-from-cart-btn {
  cursor: pointer;
  color: white;
  font-size: 125%;
  border-color: silver;
  background-color: #9e1010;
  padding: 15px;
  outline-offset: -5px;
  outline: black 3px solid;
}

.CartItem .Remove-from-cart-btn:hover {
  background-color: #c71010;
}

.CartItem .Remove-from-cart-btn:active {
  border-color: red;
}

.quantity {
  display: flex;
  flex-flow: wrap row;
  justify-content: space-evenly;
  margin: 25px;
}

.quantity .plus, .quantity .minus, .quantity span {
  font-size: 25px;
  color: white;
  cursor: pointer;
  border: none;
  background: none;
  padding: 5px 25px;
  margin: 0;
}

.quantity span {
  cursor: unset;
}

.quantity .plus {

}

.quantity .minus {

}

.quantity .plus:hover {
  color: blue;
}

.quantity .minus:hover {
  color: red;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

